}

@media screen and (max-width: 768px) {
    .right__header{
        display: none;
        
    }
    .admin__toggle__menu{
        display: block;
    }
    .logout__button{
        display: block;
    }
    

    .middle__header>ul{
        position: fixed;
        display: block;
        position: absolute;
        top: 70px;
        left: 0%;
        background-color: white;
        border-right: 1px solid rgba(128, 128, 128, 0.226);
        width: 30vw;
        height: 100vh;
        
    }

    .middle__header>ul{
        transition: 0.4s all ease-in;
    }

    .middle__header>ul>li{
        border-bottom: 2px solid gray;
        padding: 10px 0;
        
    }


    .middle__header>ul>li>a:hover{
        border-bottom: none;
